/* TOGGLE MENU */

.toggleMenu {
  left: 0;
  top: 0 !important;
  border: none;
  background-color: #E8E8F0;
  position: fixed;
  z-index: 99;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden; 
}


.toggleMenu::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 5px;
  width: 2px;
}


.togglePerfil {
  top: 0;
  left: 0;
  background-color: #506048;
  height: 15%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
