}

.toggleContenido {
  padding-top: 20px;
  padding-bottom: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
} 

.toggleContenido > a {
  height: 40px;
  text-decoration: none;
  -webkit-align-items: center;
          align-items: center;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  color: #506048;
  margin-bottom: 5px;
  display: -webkit-flex;
  display: flex;
  align-items: center;
} 

.toggleContenido > a > i {
  margin-left: 15px;
  margin-right: 4px;
}

.toggleContenido > a:hover {
  background-color: #ffffff;
  -webkit-transform: scale(1.08) translate(10px);
          transform: scale(1.08) translate(10px);
}


.menu_cat_toggle {
